// @flow

import * as React from "react";
import Page from "../ui/Page";
import Logo from "../ui/Logo";
import { colors } from "../ui/theme";

function Header({ children }) {
  let style = {
    color: colors.textTitle,
    fontSize: "19pt",
    textTransform: "uppercase",
    fontStyle: "italic",
    fontWeight: "800"
  };
  return <div style={style}>{children}</div>;
}

function Section({ title, children }) {
  let style = {
    marginBottom: 40,
  };
  let styleBody = {
    fontSize: "16pt",
    fontWeight: "300",
    color: '#333',
    marginTop: 20
  };
  return (
    <div style={style}>
      <Header>{title}</Header>
      <div style={styleBody}>{children}</div>
    </div>
  );
}

const whatWeDo = `
A group of software engineers.
`.trim();

function Index() {
  return (
    <Page>
      <Logo style={{ margin: "100px auto" }} />
      <Section title="Who we are">{whatWeDo}</Section>
      <Section title="Technology"></Section>
      <Section title="Projects"></Section>
      <Section title="Team"></Section>
    </Page>
  );
}

export default Index;
